.btn {
  text-decoration: none;
  border: 2px solid #17cf54;
  padding: 0.65rem 2rem;
  border-radius: 4px;
  font-size: 1rem;
  white-space: nowrap;
  display: inline-block;
}

.green-full {
  color: #fff;
  background: #0ed166;
}

.green-empty {
  color: #17cf54;
  background: transparent;
}
