/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/

html {
  box-sizing: border-box;
  @include font-size(1);
}

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit;
}

body {
  background: url('http://refilter.van.cp.academy.red/wp-content/uploads/2019/09/pattern.jpg')
    no-repeat center bottom/cover;
  color: #000;
  font-size: 14;
}

img {
  height: auto;
  max-width: 100%;
}

.container,
.container-fluid {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.container {
  width: 80%;
  max-width: 1366px;
  margin: 0 auto;
  text-align: left;
}

.container-fluid {
  max-width: 1366px;
  width: 100%;
  margin: 0 auto;
  text-align: center;
}

.container-content > * {
  max-width: 650px;
  width: 100%;
  margin: 0 auto;
  text-align: left;
}

.landing {
  min-height: 100vh;
  color: #fff;
  text-shadow: 0px 0px 2px #000000;

  @include desktop {
    min-height: 80vh;
  }

  .landing-title,
  .landing-quote {
    line-height: 1.5;
    width: 80%;
  }

  .landing-title {
    font-size: 2.4rem;
    padding-bottom: 1rem;
  }

  .landing-question {
    text-transform: uppercase;
    font-weight: 200;
    letter-spacing: 0.5rem;
    margin-bottom: 1.5rem;
  }

  .landing-quote {
    margin: 0;
    font-weight: 400;
    font-size: 2.35rem;
    margin-bottom: 2rem;
  }

