  // }
}

.wavy-newsletter {
  padding: 2rem 0;
  color: #fff;
  line-height: 1.5;

  .wavy-newsletter-title {
    font-size: 2rem;
    font-weight: 300;
    width: 80%;
  }

  input {
    width: 80%;
    padding: 0.75rem 0 1rem 0.5rem;
    margin: 0.8rem 0;
  }
}
